<template>
  <v-card flat class="mt-5">
    <v-container>
      <v-row class="justify-center">
        <v-col cols="3">
          <template v-for="(user, index) in information.usersOpportunitySales">
            <span :key="index" class="d-block">
              <b>{{ user.name }}</b>
              :
              {{
                filterBy === 'sum'
                  ? `$${user[filterBy].toFixed(2)}`
                  : user[filterBy]
              }}
            </span>
          </template>
        </v-col>
        <v-col cols="9">
          <v-row>
            <v-col>
              <span
                class="d-block"
                v-for="(item, index) in getInfo.firstColumn"
                :key="index"
              >
                <b>{{ columnOne[index] }}</b>
                : {{ item }}
              </span>
            </v-col>
            <v-col>
              <span
                class="d-block"
                v-for="(item, index) in getInfo.secondColumn"
                :key="index"
              >
                <b>{{ columnTwo[index] }}</b>
                : ${{ item }}
              </span>
            </v-col>
            <v-col>
              <span
                class="d-block"
                v-for="(item, index) in getInfo.thirdColumn"
                :key="index"
              >
                <b>{{ columnThree[index] }}</b>
                : ${{ item }}
              </span>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-container>
  </v-card>
</template>
<script>
export default {
  name: 'CardOpportunitySaleInformation',
  props: {
    information: {
      type: Object,
      default: () => {
        return {
          usersOpportunitySales: [],
          periodsOpportunitySales: [],
        };
      },
    },
    filterBy: {
      type: String,
      default: 'count',
    },
  },
  data() {
    return {
      columnOne: [
        'Este perodo',
        'Perodo pasado',
        'Mismo perodo el mes anterior',
      ],
      columnTwo: [
        'Ingresos este perodo',
        'Ingresos perodo pasado',
        'Ingresos mismo perodo el mes anterior',
      ],
      columnThree: [
        'Ingresos totales este perodo',
        'Ingresos totales perodo pasado',
        'Ingresos totales mismo perodo el mes anterior',
      ],
    };
  },
  computed: {
    getInfo() {
      const firstColumn = this.information.periodsOpportunitySales.map(
        (item) => {
          return item.count;
        },
      );

      const secondColumn = this.information.periodsOpportunitySales.map(
        (item) => {
          return item.sum;
        },
      );

      const thirdColumn = this.information.periodsOpportunitySales.map(
        (item) => {
          return item.totalSum;
        },
      );

      return { firstColumn, secondColumn, thirdColumn };
    },
  },
};
</script>

